<script setup>
    import { useRouter } from 'vue-router'
    //跳转到game
    //#region
    const router = useRouter(); // router 对应了 vue2 中的 $router 对象
    let defList = JSON.stringify({histry:[]})
    //读取本地数据
    let gamelist = localStorage.getItem("gamelist")||defList;
    //判断本地数据是否存在
    if (gamelist) {
      //数据回显
      gamelist = JSON.parse(gamelist);
    }
    const goback = () => {
      router.push('/')
    };
</script>
